<template>
  <div class="animation-number__container">
    <div
      class="animation-bumber-content"
      :class="{'with-transition': withTransition}"
      :style="style"
    >
      <div>0</div>
      <div>1</div>
      <div>2</div>
      <div>3</div>
      <div>4</div>
      <div>5</div>
      <div>6</div>
      <div>7</div>
      <div>8</div>
      <div>9</div>
      <div>0</div>
    </div>
  </div>
</template>

<script>
const DEFAULT_NUMBER = 8
const NUMBER_HEIGHT = 100

function getNumberStyle(number) {
  const intNumber = parseInt(number, 10)
  return {
    transform: `translateY(-${intNumber * NUMBER_HEIGHT}px)`
  }
}

export default {
  name: 'DateCounter',
  props: {
    number: {
      type: [Number, String],
      default: DEFAULT_NUMBER,
    },
  },
  data() {
    return {
      withTransition: true,
      style: '',
      nextZero: false,
    }
  },
  computed: {
    intNumber() {
      return parseInt(this.number, 10)
    },
    // style: function () {
    //   return getNumberStyle(this.number)
    // },
  },
  watch: {
    intNumber: function(currentNumber, oldNumber) {
      if (oldNumber === currentNumber) {
        return
      }
      // 如果是从 9 到 0， 仍然顺序动效
      if (oldNumber === 9 && currentNumber === 0) {
        this.nextZero = true
        this.setStyle(10)
        this.handleZero()
      } else {
        this.setStyle(currentNumber)
      }
    },
  },
  mounted() {
    this.setStyle(this.number)
  },
  methods: {
    setStyle(targetNumber) {
      this.style = getNumberStyle(targetNumber)
    },
    // 当是从9到0切换时，动效结束需要先重置到真的0
    handleZero() {
      this.$nextTick(() => {
        setTimeout(() => {
          this.withTransition = false
          this.nextZero = false
          // 将位置重置到0
          this.setStyle(0)

          setTimeout(() => {
            this.withTransition = true
          }, 300)
        }, 500)
      })
    }
  }
}
</script>

<style scoped>
  .animation-number__container {
    height: 100px;
    overflow: hidden;
  }

  .animation-bumber-content {
    font-size: 100px;
    line-height: 100px;
    transform: translateY(0)
  }

  .with-transition {
    transition: transform 0.5s ease-in-out;
  }

  .animation-bumber-content {

  }
</style>
