<template>
  <div class="date-counter">
    <div class="date-counter__title">
      TIME IS LOVE
    </div>
    <div class="date-counter__content">
      <div class="date-counter__item">
        <div class="data-counter__number">
          <AnimationNumbers :number="durationSinceDate.days" />
        </div>
        <div class="date-counter__unit">
          天
        </div>
      </div>
      <div class="date-counter__item">
        <div class="data-counter__number">
          <AnimationNumbers :number="durationSinceDate.hours" />
        </div>
        <div class="date-counter__unit">
          小时
        </div>
      </div>
      <div class="date-counter__item">
        <div class="data-counter__number">
          <AnimationNumbers :number="durationSinceDate.mins" />
        </div>
        <div class="date-counter__unit">
          分
        </div>
      </div>
      <div class="date-counter__item">
        <div class="data-counter__number">
          <AnimationNumbers :number="durationSinceDate.secs" />
        </div>
        <div class="date-counter__unit">
          秒
        </div>
      </div>
    </div>
  </div>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .date-counter {
    overflow: hidden;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .date-counter__title {
    color: #900519;
    font-size: 80px;
    font-family: 'PingFangSC-UltraLight', Helvetica, Arial, sans-serif;
  }

  .date-counter__content {
  }

  .date-counter__item {
    float: left;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }

  .data-counter__number {
  }

  .date-counter__unit {
    color: #900519;
  }

  @media screen and (max-width: 400px) {
    .date-counter__title {
      font-size: 50px;
    }

    .date-counter__content {
      flex-direction: column;
      align-items: flex-start;
    }
  }
</style>


<script>
import { getTimeDuration } from '../../common/dateUtil'
import { prefix2Num } from '../../common/util'
import AnimationNumbers from '../AnimationNumbers'
const NOW_DATE = new Date()
// 下楼时间为 2019-01-05 04:30
const FIRST_DATE = new Date(2019, 0, 5, 4, 30)
const nowSinceFirstDate = NOW_DATE.getTime() - FIRST_DATE.getTime()

export default {
  name: 'DateCounter',
  components: {
    AnimationNumbers,
  },
  props: {
  },
  data() {
    return {
      sinceDate: nowSinceFirstDate
    }
  },
  computed: {
    durationSinceDate() {
      const duration = getTimeDuration(this.sinceDate)
      return {
        days: prefix2Num(duration.days),
        hours: prefix2Num(duration.hours),
        mins: prefix2Num(duration.mins),
        secs: prefix2Num(duration.secs)
      }
    }
  },
  mounted() {
    setInterval(()=> {
      this.sinceDate = this.sinceDate + 1000
    }, 1000)
  },
}
</script>