<template>
  <div class="animation-numbers__container">
    <div
      v-for="(singleNumber, index) in numbers"
      :key="numbers.length - index"
      class="animation-numbers__number"
    >
      <SingleAnimationNumber
        :number="singleNumber"
      />
    </div>
  </div>
</template>

<style scoped>
  .animation-numbers__container {
    overflow: hidden;
  }

  .animation-numbers__number {
    float: left;
    margin: 0 8px;
  }
</style>

<script>
import SingleAnimationNumber from './SingleAnimationNumber'

export default {
  name: 'AnimationNumbers',
  components: {
    SingleAnimationNumber,
  },
  props: {
    number: {
      type: String,
      default: '0',
    },
  },
  computed: {
    // 将number生成到每一位
    numbers: function () {
      const numberStr = `${this.number}`
      const numberArray = numberStr.split('')
      return numberArray
    },
  },
}
</script>
