// 获取指定时间戳对应的间隔
const ONE_HOUR_TIMESTAMP = 3600 * 1000
const ONE_DAY_TIMESTAMP = 24 * ONE_HOUR_TIMESTAMP
const ONE_MIN_TIMESTAMP = 60000

export function getTimeDuration(timestamp) {
  // 剩余天数
  const days = Math.floor(timestamp / ONE_DAY_TIMESTAMP)
  let remainTimeStamp = timestamp % ONE_DAY_TIMESTAMP

  // 剩余小时数
  const hours = Math.floor(remainTimeStamp / ONE_HOUR_TIMESTAMP)
  remainTimeStamp %= 3600 * 1000

  // 剩余分钟数
  const mins = Math.floor(remainTimeStamp / ONE_MIN_TIMESTAMP)

  remainTimeStamp %= ONE_MIN_TIMESTAMP

  // 剩余秒数
  const secs = Math.floor(remainTimeStamp / 1000)

  remainTimeStamp %= 1000

  return {
    days,
    hours,
    mins,
    secs,
  }
}